import React from "react"
import Layout from "../components/global/global_layout"
import ColumnOf2 from "../components/layout/column_of_2"
import KeyVisual from "../components/partial/key_visual"
import BlogList from "../components/partial/blog_list"
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize } from '../util/style_property'
import SideBar from "../components/partial/side_bar"
import SEO from "../components/seo"

export const query = graphql`
  query($slug: String!){
    contentfulTag(slug: {eq: $slug}){
      name
    }
    allContentfulArticle(
      sort: {
        fields: updatedAt,
        order: DESC
      }
      filter: {
        tags: {
          elemMatch: {
            slug: {in: [$slug]}
          }
        }
      }
    ){
      edges{
        node{
          title
          slug
          createdAt(formatString: "YYYY-MM-DD")
          tags{
            name
          }
          thumbnail{
            fluid{
              src
            }
          }
        }
      }
    }
  }
`

const Wrapper = styled.div`
  width: 100%;
  margin: auto;
  background-color: ${color.background_dark_gray};
`

function TagPage(props) {
  const tagName = props.data.contentfulTag.name
  const pageEdges = props.data.allContentfulArticle.edges
  return (<Layout>
    <SEO title={tagName} />
    <KeyVisual />
    <Wrapper>
      <ColumnOf2 aside={<SideBar />}>
        <BlogList edges={pageEdges} prependHeadingText={"「" + tagName + "」"} isTagFilted={true} />
      </ColumnOf2>
    </Wrapper>
  </Layout>)
}
export default TagPage