import React from "react"
import { graphql, useStaticQuery} from 'gatsby'
import styled from 'styled-components'
import BlogItem from './blog_item'
import DropButton from './drop_button'
import { color, padding, margin, borderRadius, textSize , boxShadow, breakpoint} from '../../util/style_property'

const BlogList = ({ edges, prependHeadingText = "", isTagFilted = false}) => {

  const Wrapper = styled.section`
    display: block;
    width: 100%;
    padding: ${padding.m};
    background-color: ${color.background_light_gray};
    border-radius: ${borderRadius.m};
    box-shadow: ${boxShadow.s};

    @media screen and (max-width: ${breakpoint.mobile_l}) {
      border-radius: 0;
    }
  `

  const Header = styled.div`
    display: flex;
    justify-content: space-between;

    .drop-button{
      height: ${textSize.h1};
      margin: 0 ${margin.xs};
      .inner{
        line-height: ${textSize.h1};
      }
    }

    @media screen and (max-width: ${breakpoint.mobile_l}) {
      flex-flow: column;
      .drop-button{
        display: block;
        order: 1;
        width: 146px;
        margin: 0 0 ${margin.m} auto;
      }
      h2{
        order: 2;
      }
    }
  `

  const Heading = styled.h2`
    flex: 1;
    height: ${textSize.h1};
    line-height: ${textSize.h1};
    margin: 0;
    color: ${color.text_black};
    font-size: ${textSize.h2};
    box-sizing: border-box;
    padding: 0 ${padding.s} 0 ${padding.m};
    border-left: ${padding.s} solid ${color.theme_color};
    background-color: ${color.white};
  `

  const headingText = isTagFilted ? prependHeadingText + "の記事一覧" : "記事一覧"
  const toTopButton = isTagFilted ? <DropButton to="/">記事一覧に戻る</DropButton> : null

  return (
    <Wrapper>
      <Header>
        <Heading>{headingText}</Heading>
        {toTopButton}
      </Header>
      <BlogItems edges = {edges} />
    </Wrapper>
  )
}

const BlogItems = ({edges}) => {
  const items = edges.map((edge) => {
    let node = edge.node
    let src = node.thumbnail ? node.thumbnail.fluid.src : ""
    return (
      <BlogItem
        title={node.title}
        date={node.createdAt}
        src={src}
        slug={node.slug}
      />)
  })

  return items
}

export default BlogList